import { Promise} from 'core-js';
import bbox from 'geojson-bbox';
import consts from "./consts";
const t = require('@turf/turf');

export default {
    en2kh(en) {
        const khm = '';
        const eng = '0123456789';
        var kh = en.toString();
        for (let i = 0; i < eng.length; i++) {
            kh = kh.replaceAll(eng[i], khm[i]);
        }
        kh = kh.replaceAll('.', ',');
        return kh;
    },
    getExpiryStatus(feature){
        const expiry = feature.properties.lic_expiry;
        if(!expiry) { return consts.expiry.NA; }
        if(new Date(expiry) < new Date()) { return consts.expiry.expired; }
        return consts.expiry.active;
    },

    getBounds(features){
        const b = bbox(features);
        const bounds = [{lat:b[1],lng:b[0]},{lat:b[3],lng:b[2]}];
        return bounds;
    },
    
    getYear(feature) {
        const yearNotFound = consts.labelOtherYears;
        const date = this.getDate(feature);
        if(!date) return yearNotFound;
        const year = date.getFullYear();
        return isNaN(year)? yearNotFound : year;
    },
    getDate(feature){
        const licDate = feature.properties.lic_date;
        if(!licDate) return null;
        return new Date(licDate);
    },
    getType(feature){ return feature.properties.lic_type; },
    getCode(feature) { return feature.properties.pro_code; },

    getLabelAndData(map){
        const labels = Object.keys(map);
        const data = Object.values(map);
        return {labels, data};
    },
    featureSelected(selectedFeatures, feature){
        const gjson = this.geojson.filterByProperty(selectedFeatures, 'pro_code', this.getCode(feature));
        return gjson.features.length > 0;
    },

    // COUNT BY
    countValuesInMap(map){
        const newMap = new Map();
        for (const key in map) {
            if (Object.hasOwnProperty.call(map, key)) {
                const elements = map[key];
                newMap[key] = elements.length;
            }
        }
        return newMap;
    },
    countFeaturesByTypes(features){
        const types = this.groupFeaturesByTypes(features);
        return this.countValuesInMap(types);
    },
    countFeaturesByYears(features){
        const years = this.groupFeaturesByYears(features);
        return this.countValuesInMap(years);
    },
    countFeaturesByProvinces(features){
        const provinces = this.groupFeaturesByProvinces(features);
        return this.countValuesInMap(provinces);
    },

    // GROUP BY
    groupFeaturesBy(xs, key) {
        return xs.reduce((rv, x) => {
          (rv[x.properties[key]] = rv[x.properties[key]] || []).push(x);
          return rv;
        }, {});
    },
    groupFeaturesByTypes(features){
        return this.groupFeaturesBy(features, 'lic_type');
    },
    groupFeaturesByYears(features){
        const years = new Map();
        for (const f in features) {
            if (Object.hasOwnProperty.call(features, f)) {
                const feature = features[f];
                const year = this.getYear(feature);
                const current = years[year];
                years[year] = current? [...current, feature] : [feature];
            }
        }
        return years;
    },
    groupFeaturesByProvinces(features){
        const provinces = new Map();
        for (let i = 0; i < features.length; i++) {
            const f = features[i];
            if(!f.properties || !f.properties.loc_khet) continue;
            const ps = f.properties.loc_khet.split(',');
            for (let j = 0; j < ps.length; j++) {
                const p = ps[j].trim();
                const current = provinces[p];
                provinces[p] = current? [...current, f]: [f];
            }
        }        
        return provinces;
    },

    // GEOJSON
    geojson: {
        empty(){ return {features:[]}; },
        create(features, name='features') { return {
            name,
            features,
            type: 'FeatureCollection',
            crs: { 
                type: 'name',
                name: 'urn:ogc:def:crs:OGC:1.3:CRS84',
            },
        }},

        buffer(geojson, size){
            return t.buffer(geojson, size);
        },

        bufferAsync(geojson, size){
            return new Promise((resolve, reject) => {
                try {
                    const result = this.buffer(geojson, size);
                    resolve(result);
                } catch (error) {
                    reject(error);
                }
                
            });
        },

        isEmpty(geojson){
            return geojson.features.length === 0;
        },

        filter(geojson, filters){
            const features = geojson.features.filter(f => filters(f));
            return {...geojson, features: features};
        },

        filterByProperty(geojson, propertyName, value, filters){
            if(!filters) {return this.filter(geojson, (f) => {return f.properties[propertyName] === value});}
            return this.filter(geojson, (f) => {return f.properties[propertyName] === value && filters(f)});
        },

        setStyleType(geojson, type){
            const features = geojson.features.filter(f => {
                f.properties.stype = type;
                return f;
            });
            return {...geojson, features};
        },

        features2DataTable(features){
            return features.map(f => {
                                const properties = f.properties;
                                properties.feature = f;
                                return properties
                            });
        },

        dataTable2Features(data){
            return data.map(d => d.feature);
        },
        dataTable2GeoJson(data){
            return this.create(this.dataTable2Features(data));
        }
    },

    


    fetch: {
        fetch(path, callback){
            // const url = 'https://eia.moe/d/';
            const url = "https://eia-moe.github.io/map-dist/d/";
            const request = require('request');
            const options = { json: true };
            const fullUrl = `${url}${path}`;
            console.log('Fetching data from...', `${url}${path}`);
            request(fullUrl, options, callback);
        },
        projectsAsync(){
            return new Promise((resolve, reject) =>
                this.fetch('projects', (error, res, body) => {
                    if (error) { reject(error); };
                    if (!error && res.statusCode == 200) {
                        resolve(body);
                    }else{
                        reject(body);
                    }
                    
                })
            );
        },
        riverAsync(){
            return new Promise((resolve, reject) =>
                this.fetch('mekong', (error, res, body) => {
                    if (error) { reject(error); };
                    if (!error && res.statusCode == 200) {
                        resolve(body);
                    }else{
                        reject(body);
                    }
                    
                })
            );
        }
    }
}