<template>
<div class="selector">
    {{title || ''}}
    
      
    <select v-model="selectedItem" @change="selectChanged">
        <option value="0"></option>
        <option value="1"></option>
        <option value="2"></option>
        <option value="3"></option>
        <option value="4"></option>
    </select>
</div>
    
</template>

<script>

export default {
    name: 'ChartAbstract',
    data(){
        return{
            selectedItem: "0"
        }
    },
    props:{
        title: String,
    },
    methods:{
        selectChanged(item){
            this.$emit('selectChanged', this.selectedItem);
        }
    }
}
</script>

<style scoped>

.selector {
    font-family: 'Nokora', sans-serif;
    font-size: 14px;
}
.selector > select {
    font-family: 'Nokora', sans-serif;
    font-weight: 700;
    font-size: 14px;
}
.selector > select > option {
    font-family: 'Nokora', sans-serif;
    font-size: 14px;
}
</style>