import Vue from 'vue'
import Vuex from 'vuex'
import consts from '../scripts/consts.js';

Vue.use(Vuex)

import func from '../scripts/helpers.js';

export default new Vuex.Store({
  state: {
    dataLoaded: false,
    projects: {features: []},
    windowWidth: 0,
    windowHeight: 0,
    filter: (feature) => { return true; },
    selectionFilter: (feature) => { return false; },
    selectionBounds: [],
    noneExpiredProjectFilter: (feature) => { return func.getExpiryStatus(feature) !== consts.expiry.expired; }
  },
  mutations: {
    initProjectList(state, projects) {
      state.projects = projects;
    },
    windowSize(state, width, height){
      state.windowWidth = width;
      state.windowHeight = height;
    }
  },

  getters: {
    geojson: (state, getters) => {
      return state.projects;
    },
    esiaGeoJson: (state, getters) =>{
      return func.geojson.filterByProperty(getters.geojson, 'lic_type', 'ESIA', state.noneExpiredProjectFilter)
    },
    iesiaGeoJson: (state, getters) =>{
      return func.geojson.filterByProperty(getters.geojson, 'lic_type', 'IESIA', state.noneExpiredProjectFilter)
    },
    epcGeoJson: (state, getters) =>{
      return func.geojson.filterByProperty(getters.geojson, 'lic_type', 'EPC', state.noneExpiredProjectFilter)
    },
    
    expiredGeoJson: (state, getters) =>{
      return func.geojson.filter(getters.geojson, (feature) => { return func.getExpiryStatus(feature) === consts.expiry.expired; })
    },

    selectedFeatures: (state, getters) =>{
      return func.geojson.filter(getters.geojson, state.selectionFilter);
    },
    selectionBounds: (state, getters) => { return state.selectionBounds; },

    windowWidth: (state) => {
      return state.windowWidth;
    }
  },

  actions: {
    setFilter({state}, filter){ state.filter = filter; },
    setSelectionFilter({state}, filter){ state.selectionFilter = filter; },
    clearSelectionFilter({state}){ state.selectionFilter = (feature) => { return false; }; },
    setSelectionBounds({state}, bounds) { state.selectionBounds = bounds },
  },
  modules: {
  }
})
