import consts from "./consts.js"
import func from "./helpers.js"

const esia = {
    weight: 3,
    color: consts.colors.esia,
    opacity: 1,
    fillColor: consts.colors.fill,
    fillOpacity: 0.3,
};
const iesia = {...esia, color: consts.colors.iesia};
const epc = {...esia, color: consts.colors.epc};
const expired = {...esia, weight:2, color: consts.colors.expired, fillOpacity:0.5};
const others = {...esia, weight:2, color: consts.colors.others, fillOpacity:0.1};
const buffer = {...others,weight:1, color: consts.colors.buffer, fillOpacity:0};

export default {
    buffer(feature){ return buffer},
    bufferOption(feature){ return {style: function(feature){return buffer;}}},

    allProjectLayers(feature) {
        if(func.getExpiryStatus(feature) === consts.expiry.expired){
            return expired;
        };

        if (feature.properties.lic_type === "ESIA") {
            return esia;
        };
        
        if(feature.properties.lic_type === "IESIA"){
            return iesia;
        };
        
        if(feature.properties.lic_type === "EPC"){
            return epc;
        };

        return others;
    
    }


}