<template>
<div class="home" >
  <LMap :max-zoom="20" 
    :bounds.sync="bounds" 
    :options="mapOptions"
    @mouseover="collapseAllMenus"
    @mousedown="onMapClicked"
    >

    <LTileLayer
      :key="tileProvider.name"
      :name="tileProvider.name"
      :url="tileProvider.url"
      :attribution="tileProvider.attribution"
      :minNativeZoom="tileProvider.minNativeZoom"
      :options="{ maxNativeZoom: tileProvider.maxNativeZoom, maxZoom: 100 }"
      layer-type="base"
    />


    <LLayerGroup v-if="showEsiaLayer" layer-type="overlay" name="ESIA">
      <LGeoJson :geojson="esia"  :options="options"/>
    </LLayerGroup>

    <LLayerGroup v-if="showIesiaLayer" layer-type="overlay" name="IESIA">
      <LGeoJson :geojson="iesia"  :options="options"/>
    </LLayerGroup>

    <LLayerGroup v-if="showEpcLayer" layer-type="overlay" name="EPC">
      <LGeoJson :geojson="epc"  :options="options"/>
    </LLayerGroup>


    <LLayerGroup v-if="showExpiredLayer" layer-type="overlay" name="Expired">
      <LGeoJson :geojson="expired"  :options="options"/>
    </LLayerGroup>


    <LLayerGroup v-if="false" layer-type="overlay" name="All Projects">
      <LGeoJson :geojson="geojson" :options="options" />
    </LLayerGroup>


    <LLayerGroup layer-type="overlay" name="Selected Features">
      <LGeoJson :geojson="selectedFeatures" :options="optionSelectedFeatures" />
    </LLayerGroup>

    <LLayerGroup layer-type="overlay" name="Rivers">
      <LGeoJson :geojson="river" :options="bufferOption" />
    </LLayerGroup>

    <LLayerGroup layer-type="overlay" name="Case-Study-1 Projects">
      <LGeoJson :geojson="caseStudyProjects" :options="optionSelectedFeatures" />
    </LLayerGroup>


    <LControl class="pop-menu">
      <PopMenu @selected="menuSelected" v-model="expandMenu"/>
    </LControl>

    <LControl class="pop-menu">
      <LayerMenu 
        v-model="expandLayerMenu"
        @onBasemapChanged="onBasemapChanged"
        @esia-layer-checked="toggleEsiaLayer"
        @iesia-layer-checked="toggleIesiaLayer"
        @epc-layer-checked="toggleEpcLayer"
        @expired-layer-checked="toggleExpiredLayer"
      />
    </LControl >

    <LControl :position="'topleft'" class="custom-control-watermark watermark" >
        <img  :src="watermark" :width="windowWidth/(smallScreen()?3 :8)"/>
    </LControl>
    <LControlZoom position="topright" />
    <LControl :position="'topleft'" class="chart-panel" >
      <Dashboard v-if="showChart"  :height="300" :width="chartWidth" @close="showChart=false"/>
      <SandProjects v-if="showSandProjects"  :height="300" :width="600" 
        @close="onCloseSandProject"
        @data-river="onRiverLoaded"
        @data-projects="onSandDataProject"
      />
    </LControl>
  </LMap>
  
  
  </div>
</template>

<script>
// @ is an alias to /src
import events from "@degjs/event-aggregator";
import consts from '../scripts/consts.js';
import styles from '../scripts/styles.js';
import store from "../store";
import PopMenu from "../components/PopMenu.vue";
import LayerMenu from "../components/LayerMenu.vue";
import FloatingPanel from '@/components/FloatingPanel.vue'
import basemaps from "@/data/basemaps.json";
import Dashboard from '@/views/Dashboard.vue';
import html from '@/scripts/html-helpers.js';
import watermark from '@/assets/watermark.png';
import { latLngBounds, latLng, CRS} from "leaflet";
import func from '../scripts/helpers.js';
import {LMap, LIcon, LMarker, LControlLayers, LPopup, LPolyline, LPolygon, LRectangle, LTileLayer, LLayerGroup, LControl, LControlZoom, LGeoJson,} from "vue2-leaflet";

const points = [
  { lat: 10.79, lng: 102.14 },
  { lat: 14.525, lng: 107.0343 },
];
const bounds = latLngBounds(points);


export default {
  name: "Home",


  props:{
    windowHeight: Number,
    windowWidth: Number
  },


  components: {
    LMap,
    LIcon,
    LMarker,
    LControlLayers,
    LPopup,
    LPolyline,
    LPolygon,
    LRectangle,
    LTileLayer,
    LLayerGroup,
    LControl,
    LControlZoom,
    LGeoJson,
    PopMenu,
    LayerMenu,
    FloatingPanel,
    Dashboard,
    SandProjects: () => import('./SandProjects.vue')
  },


  async beforeCreate(){
    const projects = await func.fetch.projectsAsync();
    store.commit('initProjectList', projects);
    
  },


  data() {
    const river = func.geojson.empty();
    const caseStudyProjects = func.geojson.empty();
    return {

      // Mapping
      isLoading: true,
      watermark,
      bounds: bounds,
      mapOptions: {
        zoomControl: false,
        attributionControl: false,
        zoomSnap: true,
      },
      tileProvider: basemaps[0],
      
      // Data
      river,
      caseStudyProjects,

      // Settings
      showChart: false,
      showSandProjects: false,
      showEsiaLayer: true,
      showIesiaLayer: true,
      showEpcLayer: true,
      showExpiredLayer: false,
      expandMenu: false,
      expandLayerMenu: false,
    };
  },


  computed: {
    esia(){ return store.getters.esiaGeoJson; },
    iesia(){ return store.getters.iesiaGeoJson; },
    epc(){ return store.getters.epcGeoJson; },
    expired(){ return store.getters.expiredGeoJson; },
    selectedFeatures() { return store.getters.selectedFeatures; },

    bufferOption(){
      return {
        style: function(feature){
          return styles.buffer;
        }
      };
    },
    optionSelectedFeatures(){
      return {
        onEachFeature: this.onEachFeatureFunction,
        style: function(feature){
          return {
            fillColor: 'yellow',
            color: 'yellow',
            weight: 2,
            opacity: 0.8,
            fillOpacity: 0.6,
          };
        }
      }
    },
    options() {
      // const fillColor = this.fillColor;
      return {
        onEachFeature: this.onEachFeatureFunction,
        style: styles.allProjectLayers
      } 
    },
    onEachFeatureFunction() {
      return function(feature, layer) {
        const onClick = (e) => {
          const featureReadySelected = func.featureSelected(store.getters.selectedFeatures, feature);
          if(featureReadySelected) { return; }
          const pro_code = feature.properties.pro_code;
          store.dispatch('setSelectionFilter', (f)=> {return pro_code === f.properties.pro_code;});
        };
        layer.on('click', onClick);
        var table = html.create_table(feature);
        var options = { permanent: false, sticky: true };  
        layer.bindPopup(table, options);
      };
    },
    
    chartWidth() {
      return this.smallScreen()? this.windowWidth-23 : 600;
    }
  },


  mounted(){
    const context = this;
    events.unsubscribe(consts.events.mapGotoBounds);
    events.subscribe(consts.events.mapGotoBounds, (e) => {
        context.bounds=latLngBounds(e.data);
      });
    
    this.isLoading = true;
    setTimeout(() => {
        this.isLoading = false;
    }, 5000)
  },


  methods: {
    smallScreen(){
      return this.windowWidth < 600;
    },  
    isLandscape() {
      return this.windowWidth > this.windowHeight;
    },
    menuSelected(index){
      this.showChart = !this.showChart && index === 'show-chart';
      this.showSandProjects = !this.showSandProjects && index === 'show-sand-projects';
    },
    onBasemapChanged(basemap){
      this.tileProvider = basemap;
    },
    toggleEsiaLayer(value){
      this.showEsiaLayer = value;
    },
    toggleIesiaLayer(value){
      this.showIesiaLayer = value;
    },
    toggleEpcLayer(value){
      this.showEpcLayer = value;
    },
    toggleExpiredLayer(value){
      this.showExpiredLayer = value;
    },
    onMenuChange(value){
      this.expandMenu = value;
    },
    collapseAllMenus(){
      this.expandLayerMenu = false;
      this.expandMenu = false;
    },
    onMapClicked(){
      // store.dispatch('clearSelectionFilter');
      this.collapseAllMenus();
    },

    onRiverLoaded(river){
      console.log(river);
      this.river = river;
    },

    onCloseSandProject(){
      this.showSandProjects=false;
      this.river = func.geojson.empty();
    },

    onSandDataProject(projects){
      this.caseStudyProjects = projects;
    }

  },


  watch:{
  }


};
</script>


<style scoped>
.home {
  width: 100%;
  height: 100%;
}

.pop-menu {
  background: #fff;
  padding: 0 0.5em;
  border: 1px solid #aaa;
  border-radius: 0.1em;
}


.custom-control-watermark {
  font-size: 150%;
  font-weight: bolder;
  color: #0f0;
  text-shadow: #555;
  opacity: 1;
}

.leaflet-container {
  font-family: 'Nokora', sans-serif;
  font-size: 14px ;
} 

.chart-panel{
  transform: translateY(-100px);
}

</style>
