<template>
  <div>
    
    <vue-highcharts
      :options="options"
      :plotOptions="plotOptions"
      ref="myCharts"
    ></vue-highcharts>
  </div>
</template>

<script>
import events from "@degjs/event-aggregator";
import VueHighcharts from "vue2-highcharts";
//import SeriesLabel from "highcharts/modules/series-label";
import Highcharts from "highcharts";

import store from "../store";
import func from "../scripts/helpers.js";
import consts from '../scripts/consts.js';

//SeriesLabel(Highcharts);

export default {
  name: 'ChartAbstract',
  events: ['goto-bounds'],
  components: {
    VueHighcharts,
  },
  data() {
    return {
      options: null,
      plotOptions: null,
      Highcharts,
      
    };
  },
  methods: {
      chartSelected(index){
          this.selectedChart = index;
      },
      smallScreen() {
      return store.getters.windowWidth<=600;
    }

  },
  computed:{
    
  },
  mounted(){
    
    const geojson = store.getters.geojson;
    const features = geojson.features;
    const countByYears = func.groupFeaturesByYears(features);
    const { labels: years, data: projectsPerYear } = func.getLabelAndData(countByYears);
    const typesPerYear = projectsPerYear.map((y) => func.countFeaturesByTypes(y));

    // Series Data
    const esia = { type: "column", name: "ESIA", data: [], color: consts.colors.esia};
    const iesia = { ...esia, name: "IESIA", data: [], color: consts.colors.iesia};
    const epc = { ...esia, name: "EPC", data: [], color: consts.colors.epc};
    const expired = { ...esia, name: "Expired", data: [], color: consts.colors.expired};
    const total = {
        type: "line",
        name: "",
        data: [],
        marker: {
            lineWidth: 2,
            lineColor: Highcharts.getOptions().colors[3],
            fillColor: "white",
        },
        };


    for (const y in typesPerYear) {
      if (Object.hasOwnProperty.call(typesPerYear, y)) {
          const type = typesPerYear[y];
          // order years ??
          
          const esiaCount = type["ESIA"] ?? 0;
          const iesiaCount = type["IESIA"] ?? 0;
          const epcCount = type["EPC"] ?? 0;
          esia.data = [...esia.data, esiaCount];
          iesia.data = [...iesia.data, iesiaCount];
          epc.data = [...epc.data, epcCount];
          total.data = [...total.data, esiaCount + iesiaCount + epcCount];
      }
    }

    const totalEsia = esia.data.reduce((a,b)=> a+b, 0);
    const totalIesia = iesia.data.reduce((a,b)=> a+b, 0);
    const totalEpc = epc.data.reduce((a,b)=> a+b, 0);
    const totalProjects = totalEsia + totalIesia + totalEpc;

    const pieData = {
      type: "pie",
      name: "Total Projects",
      data: [
        {
          name: "ESIA",
          y: totalEsia,
          color: consts.colors.esia,
        },
        {
          name: "IESIA",
          y: totalIesia,
          color: consts.colors.iesia,
        },
        {
          name: "EPC",
          y: totalEpc,
          color: consts.colors.epc, 
        },
      ],
      center:  [45, 50],
      size: 100,
      showInLegend: false,
      dataLabels: {
        enabled: false,
      },
    };

    const emit = this.$emit;
    const plotOptions = {
        pie: {
          cursor: 'pointer',
          point: {
            events:{
              click: (e) => {
                const xIndex=e.point.x;
                const fType=pieData.data[xIndex].name;
                const filter=(feature) => {
                  const type=func.getType(feature);
                  return type===fType;
                };

                store.dispatch('setSelectionFilter', filter);
                const bounds=func.getBounds(store.getters.selectedFeatures);
                events.publish({ type: consts.events.mapGotoBounds, data: bounds });
              }
            }
          }
        },
        column: {
          cursor: 'pointer',
          point: {
            events:{
              click: (e) => {
                const yearIndex=e.point.x;
                const projectType=e.point.series.name;

                const filter=(feature) => {
                  const year=func.getYear(feature);
                  const type=func.getType(feature);
                  if(year === consts.labelOtherYears){
                    return year===years[yearIndex] && type===projectType;
                  }
                  return year===Number(years[yearIndex])&&type===projectType;
                };

                store.dispatch('setSelectionFilter', filter);
                const bounds=func.getBounds(store.getters.selectedFeatures);
                events.publish({ type: consts.events.mapGotoBounds, data: bounds });
              }
            }
          }
        }
      };

    const series = [esia, iesia, epc, total, pieData];
    const totalProjectsKh = func.en2kh(totalProjects);
    const data = {
      allowPointSelect: true,
      title: {
          text: ` \n ( ${totalProjectsKh} )`,
      },
      chart:{
        height: 350
      },
      xAxis: {
          categories: years,
      },
      yAxis: {
          title: {text:''}
      },
      labels: {
        items: [
          {
            html: "",
            style: {
            left: "10px",
            top: "0px",
            color: (Highcharts.theme && Highcharts.theme.textColor) || "black",},
          },
        ],
      },
      credits: { enabled: false, },
      series: series,
      plotOptions: plotOptions

    };

    this.options = data;
    
  }
};
</script>

