<template>
    <div class="panel" :style="size" >
        <div class="fa fa-times close-button" @click="$emit('close')"></div>
        <slot />
    </div>
    
</template>


<script>
export default {
    name: 'FloatingPanel',
    props:{
        width: {
            type: Number,
            default: 100
        },
        height: {
            type: Number,
            default: 300
        }
    },

    computed:{
        size(){
            return `width: ${this.width}px; height: ${this.height}px; `;
        }
    }
}
</script>


<style scoped>
.panel {
    background-color: aliceblue;
    
    padding: 1px 0px 0 0;
}

.panel{
  transform: translateY(80px);
}

.close-button {
    color: gray;
    float: right;
    padding: 3px 5px;
    border:1px solid gray;
    margin: 5px;
}
.close-button:hover {
    cursor: pointer;
}
</style>